.aside{
  background-color: rgb(255, 255, 255);
  min-width: 140px;
  min-height: calc(100vh - 80px);
}
ul li{
  list-style: none;
  color: red;
}
a{
  text-decoration: none;
  color: #27051f;
}
a.active{
  text-decoration: underline;
  color: #881b6e;
}
@media (max-width: 495px) {
  .aside{
    min-height: auto;
  }
  ul{
    display: flex;
  }
  li{
    margin-right: 6px;
  }
}